import React from 'react';
import PropTypes from 'prop-types';
import { Panel, ListItem, Button, Group, Div, Avatar, PanelHeader } from '@vkontakte/vkui';
import connect from '@vkontakte/vkui-connect';

class Home extends React.Component {

	state = {
		data: null
	};

	constructor(props){
		super(props);
		this.ShortLink = this.ShortLink.bind(this)
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppCallAPIMethodResult':
					this.setState({ data: e.detail.data.response });
					break;
				default:
					console.log(e.detail);
			}
		});
	}

	ShortLink(){
		connect.send(
			"VKWebAppCallAPIMethod",
			{
				"method": "utils.getLastShortenedLinks",
				"params": {
					"access_token": this.props.token
				}
			});
	}
	render() {
		return (
			<Panel id={this.props.id}>
				<PanelHeader>Example</PanelHeader>
				{this.props.fetchedUser &&
				<Group title="User Data Fetched with VK Connect">
					<ListItem
						before={this.props.fetchedUser.photo_200 ? <Avatar src={this.props.fetchedUser.photo_200}/> : null}
						description={this.props.fetchedUser.city && this.props.fetchedUser.city.title ? this.props.fetchedUser.city.title : ''}
					>
						{`${this.props.fetchedUser.first_name} ${this.props.fetchedUser.last_name}`}
					</ListItem>
				</Group>}
				<Group title="Data">
					<Div>
						{this.state.data}
						<Button size='md' onClick={this.ShortLink()}>
							Get
						</Button>
					</Div>
				</Group>

				<Group title="Navigation Example">
					<Div>
						<Button size="xl" level="2" onClick={this.props.go} data-to="persik">
							Show me the Persik, please
						</Button>
					</Div>
				</Group>
			</Panel>
		)
	}
}

export default Home;
